body {
    font-family: 'Font';
    background-color: #12130F;
  }
  
  @font-face {
    font-family: 'Font';
    src: url("fonts/UniNeueRegular-Italic.ttf");
  }
  
  @font-face {
    font-family: 'Bold';
    src: url("fonts/UniNeueHeavy-Italic.ttf");
  }
  
  /* навбар */
  
  .topnav {
    background-color: #333;
    overflow: hidden;
  }
  
  .topnav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .topnav a.active {
    background-color: #8FCB9B;
    color: #12130F;
  }
  
  /* основной контент */
  
  .title {
    color: #8FCB9B;
    font-family: 'Bold';
    text-align: center;
  }
  
  .main {
    margin: auto;
    width: 35%;
    background-color: #8FCB9B;
    border-radius: 15px;
    padding-bottom: 10px;
  }
  
  .main img {
    padding-bottom: 10px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  }
  
  .main p {
    padding-top: 10px;
    text-align: center;
    color: #12130F;
  }
  
  .main a {
    color: #7a8165;
  }
  
  /* костыль для ссылки */
  
  .tpn {
    text-decoration: none;
    color: #12130F;
  }
  
  /* картинки соц. сетей */
  
  .img {
    width: 10%;
    padding-bottom: 10px;
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
  }
  
  /* список проектов */
  
  ul center {
    padding-bottom: 5px;
  }

  ul center a {
    text-decoration: none;
  }
  
  ul center button {
    margin-left: -30px;
    padding: 10px;
    display: block;
    width: 75%;
    font-family: 'Font';
    border-radius: 15px;
    border: none;
    background-color: #333;
    color: #8FCB9B;
  }
  
  ul center button:hover {
    margin-left: -30px;
    padding: 10px;
    display: block;
    width: 75%;
    font-family: 'Font';
    border-radius: 15px;
    border: none;
    background-color: #b9dfc0;
    color: #12130F;
  }
  
  ul center button:disabled:hover {
    margin-left: -30px;
    padding: 10px;
    display: block;
    width: 75%;
    font-family: 'Font';
    border-radius: 15px;
    border: none;
    background-color: #555;
    color: #489d59;
  }
  
  button:disabled, button[disabled] {
    margin-left: -30px;
    padding: 10px;
    display: block;
    width: 75%;
    font-family: 'Font';
    border-radius: 15px;
    border: none;
    background-color: #555;
    color: #489d59;
  }
  
  /* копирайт */
  
  h2 {
    color: #8FCB9B;
    font-family: 'Bold';
    text-align: center;
  }
  
  /* прочее */
  
  .errBtn {
    padding: 25px;
    width: 10%;
    border-radius: 15px;
    border: none;
    background-color: #8FCB9B;
    color: #333;
    font-family: 'Bold';
    font-size: 24px;
  }